<template>
  <div class="control_add">
    <div class="content">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>添加商品</span>
        </div>
        <div class="text item">
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="商品名称" prop="goodsname">
              <el-input v-model="ruleForm.goodsname"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goodscategory">
              <el-select v-model="ruleForm.goodscategory" placeholder="请选择商品分类">
                <el-option label="食品" value="shanghai"></el-option>
                <el-option label="甜品饮品" value="beijing"></el-option>
                <el-option label="夜宵" value="beijing"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="商品特色" prop="goodsfeature">
              <el-checkbox-group v-model="ruleForm.goodsfeature">
                <el-checkbox label="新品上市" name="goodsfeature"></el-checkbox>
                <el-checkbox label="第二单半价" name="goodsfeature"></el-checkbox>
                <el-checkbox label="主打产品" name="goodsfeature"></el-checkbox>
                <el-checkbox label="火爆产品" name="goodsfeature"></el-checkbox>
                <el-checkbox label="祖传手艺" name="goodsfeature"></el-checkbox>
                <el-checkbox label="源自四川" name="goodsfeature"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>

            <el-form-item label="商品图片" prop="imgUrl">
              <el-upload
                class="avatar-uploader"
                action="http://127.0.0.1:3333/goods.php?a=uploadImg"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload"
              >
                <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>

            <el-form-item label="是否促销" prop="isPromotion">
              <el-radio-group v-model="ruleForm.isPromotion" style="float:left; margin-top:14px;">
                <el-radio label="促销"></el-radio><el-radio label="不促销"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="食品规格" prop="standard" style="float:left;">
              <el-radio-group v-model="ruleForm.standard">
                <el-radio label="单规格"></el-radio>
                <el-radio label="多规格"></el-radio>
              </el-radio-group>
            </el-form-item>
            <div style="clear:both;"></div>
            <el-form-item label="包装费" prop="packprice" style="float:left;">
              <el-input-number v-model="ruleForm.packprice" :step="2"></el-input-number>
            </el-form-item>
            <div style="clear:both;"></div>

            <el-form-item label="价格" prop="price" style="float:left;">
              <el-input-number v-model="ruleForm.price" :step="2"></el-input-number>
            </el-form-item>
            <div style="clear:both;"></div>

            <el-form-item label="商品描述" prop="goodsdesc">
              <el-input type="textarea" v-model="ruleForm.goodsdesc"></el-input>
            </el-form-item>
            <el-form-item style="float:left;">
              <el-button type="primary" @click="submitForm('ruleForm')">立即添加</el-button>
              <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
            <div style="clear:both;"></div>
          </el-form>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      ruleForm: {
        goodsname: "",
        goodscategory: "食品",
        goodsfeature:[],
        isPromotion: "促销",
        standard: "多规格",
        packprice: "2",
        price: "18",
        goodsdesc: ""

        // packing: "",
        // price: "",
        // date1: "",
        // date2: "",
        // delivery: false,
      },
      imageUrl: "",
      rules: {
        goodsname: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 3, message: "长度在3个字符以上", trigger: "blur" }
        ],
        goodscategory: [
          { required: true, message: "请选择商品分类", trigger: "change" }
        ],

        goodsfeature: [
          {
            type: "array",
            required: true,
            message: "请至少选择一个活动性质",
            trigger: "change"
          }
        ],
        isPromotion: [
          { required: true, message: "请选择活动类型", trigger: "change" }
        ],
        goodsdesc: [
          { required: true, message: "请填写商品描述", trigger: "blur" }
        ]
      },
      dialogVisible: false,
      disabled: false
    };
  },
  methods: {
    submitForm() {
      this.$refs.ruleForm.validate(result => {
        if (result) {
          axios
            .post("http://127.0.0.1:3333/goods.php?a=add",`goodsname=${this.ruleForm.goodsname}&goodscategory=${this.ruleForm.goodscategory}&goodsfeature=${this.ruleForm.goodsfeature}&imgUrl=${this.imageUrl}&isPromotion=${this.ruleForm.isPromotion}&standard=${this.ruleForm.standard}&packprice=${this.ruleForm.packprice}&price=${this.ruleForm.price}&goodsdesc=${this.ruleForm.goodsdesc}`)
            .then(data => {
              // console.log(data);
              if (data.data.code == 0) {
                alert(data.data.msg);
                this.$router.push('/index/control_list')
              } else {
                alert(data.data.msg);
              }
            });
        } else {
          // console.log("error submit!!");
          return false;
        }
      });
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleAvatarSuccess(res) {
      // this.imageUrl = URL.createObjectURL(file.raw);
       this.imageUrl ="http://127.0.0.1:3333/"+res.data;
      // console.log(this.imageUrl)
      // console.log(res.data)
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    }
  }
};
</script>

<style>
.control_add .content {
  width: 100%;
  height: 100%;
  background: #fff;
}
.control_add .el-form {
  width: 800px;
}
.control_add .el-form .el-select {
  float: left;
}
.control_add
  .el-form-item.is-required:not(.is-no-asterisk)
  .el-form-item__label-wrap
  > .el-form-item__label:before,
.el-form-item.is-required:not(.is-no-asterisk) > .el-form-item__label:before {
  content: "";
  color: #f56c6c;
  margin-right: 4px;
}
.control_add .text {
  padding-top: 30px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>